<script>
import { ref } from 'vue';
import axios from 'axios';
export default {
  name: 'CustomerCenter',
  setup() {
    let about = ref(true);
    let rule = ref(false);
    let qna = ref(false);
    let credit = ref(false);
    let qnaText = ref('');
    let qnaList = ref([]);
    let nickName = ref('');


    axios.get('/api/customerCenter/nonMemberQna')
    .then(res=>{
      qnaList.value = res.data;
    })
    .catch(err=>{
      console.log(err);
    })
    function clickAbout() {
      credit.value = false;
      about.value = true;
      rule.value = false;
      qna.value = false;
    }

    function clickRule() {
      credit.value = false;
      about.value = false;
      rule.value = true;
      qna.value = false;
    }

    function clickQna() {
      credit.value = false;
      about.value = false;
      rule.value = false;
      qna.value = true;
    }

    function clickCredit() {
      credit.value = true;
      about.value = false;
      rule.value = false;
      qna.value = false;
    }

    function inputNick(e) {
      nickName.value = e.target.value;
    }

    function inputText(e) {
      if(e.target.value.length <= 100) {
        qnaText.value = e.target.value;
      } else {
        alert('100   .');
        e.target.value = qna.value;
      }
    }

    function qnaSubmit() {
      if(nickName.value === '') {
        alert('  ');
      } else if(qnaText.value === '') {
        alert('  .');
      } else {
        let qnaData = {
          nickName : nickName.value,
          text : qnaText.value
        }
        axios.post('/api/customerCenter/qnaSubmit', qnaData)
        .then((result)=>{
            qnaText.value = '';
            alert('.');
            qnaList.value = result.data;
        })
        .catch(err=>{
          console.log(err);
        })
      }
    }

    return {clickAbout, clickRule, clickQna, about, qna, rule, qnaText, qnaList, nickName, qnaSubmit, inputText, inputNick, credit, clickCredit};
  }
}
</script>

